//fonts
@mixin font($alias, $name) {
   @font-face {
      font-family: $alias;
      src: url("../fonts/" + $name + ".eot");
      src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".ttf") format("truetype");
      font-weight: normal;
      font-style: normal; } }

@include font('Manrope-Light', 'Manrope-Light');
@include font('Manrope-Bold', 'Manrope-Bold');
@include font('Manrope-Medium', 'Manrope-Medium');
@include font('Manrope-Regular', 'Manrope-Regular');
@include font('Manrope-Semibold', 'Manrope-Semibold');
@include font('ProximaNova-Black', 'ProximaNova-Black');
@include font('ProximaNova-Bold', 'ProximaNova-Bold');
@include font('ProximaNova-Extrabld', 'ProximaNova-Extrabld');
@include font('ProximaNova-Regular', 'ProximaNova-Regular');
@include font('ProximaNova-Semibold', 'ProximaNova-Semibold');

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
   $font-family: null;
   @if $name == "man300" {
      $font-family: "Manrope-Light"; }
   @if $name == "man700" {
      $font-family: "Manrope-Bold"; }
   @if $name == "man500" {
      $font-family: "Manrope-Medium"; }
   @if $name == "man400" {
      $font-family: "Manrope-Regular"; }
   @if $name == "man600" {
      $font-family: "Manrope-Semibold"; }
   @if $name == "pn900" {
      $font-family: "ProximaNova-Black"; }
   @if $name == "pn700" {
      $font-family: "ProximaNova-Bold"; }
   @if $name == "pn800" {
      $font-family: "ProximaNova-Extrabld"; }
   @if $name == "pn400" {
      $font-family: "ProximaNova-Regular"; }
   @if $name == "pn600" {
      $font-family: "ProximaNova-Semibold"; }
   font-family: $font-family, Arial, sans-serif;
   @if $font-family != null {
      font-weight: normal; }
   @if $font-size != null {
      font-size: $font-size * 1px; }
   @if $line-height != null {
      line-height: $line-height * 1px; }
   @if $letter-spacing != null {
      letter-spacing: ($letter-spacing / 100) * 1px; } }
