  color: #363C43;
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container {
  position: relative;
  z-index: 1;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  margin: 0 -15px;
}

.col {
  padding: 0 15px;
}

.col_sm {
  flex: 0 1 33.33333%;
  width: 33.33333%;
}

.main {
  display: block;
  overflow: hidden;
}

.btn {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 21px 30px;
  outline: 0;
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.36px;
  text-align: center;
  border-radius: 0;
  cursor: pointer;
  background-color: #D5B893;
  color: #fff;
  text-transform: uppercase;
  transition: .3s ease-in-out;
}

.btn:hover {
  background-color: #c49b67;
}

.btn:hover::before {
  transform: translate(-9px, -9px);
}

.btn::before {
  position: absolute;
  bottom: -9px;
  left: 9px;
  z-index: 1;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(220, 179, 133, 0.26);
  transition: .4s ease-in-out;
  pointer-events: none;
  content: '';
}

.btn_ls {
  letter-spacing: 1.8px;
}

.btn span {
  position: relative;
  z-index: 1;
}

.btn__decor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.btn__decor::before {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(110deg, rgba(255, 255, 255, 0) 15%, rgba(255, 255, 255, 0.75) 25%, rgba(255, 255, 255, 0) 35%);
  animation: btnBlink 2.5s linear infinite;
  content: '';
}

.has-bg {
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.has-bg::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  content: '';
}

.has-bg_1::before {
  background-color: rgba(34, 29, 14, 0.85);
}

.has-bg_2::before {
  background-color: rgba(36, 20, 3, 0.66);
}

.has-bg_3::before {
  background-color: rgba(36, 20, 3, 0.8);
  content: '';
}

.link-decor {
  position: relative;
  white-space: nowrap;
}

.link-decor:hover::after {
  opacity: 0;
}

.link-decor::after {
  position: absolute;
  bottom: 0;
  left: 2px;
  right: 0;
  z-index: 1;
  height: 1px;
  background-color: rgba(213, 184, 147, 0.7);
  transition: .3s ease-in-out;
  content: '';
}

