.diamond-shape {
  position: relative;
  z-index: 2;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: "ProximaNova-Black", Arial, sans-serif;
  font-weight: normal;
  font-size: 38px;
  line-height: 46px;
  letter-spacing: 0.63px;
  width: 80px;
  height: 80px;
  color: #21262D;
}

.diamond-shape::after {
  position: absolute;
  top: 0;
  left: calc(50% - 40px);
  z-index: 1;
  transform: rotate(45deg);
  width: 100%;
  height: 100%;
  background-color: #F9F2EA;
  content: '';
}

.diamond-shape__title {
  z-index: 3;
}

.diamond-shape__title-descr {
