  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
}

.diamond-shape__icon {
  position: relative;
  top: 5px;
  z-index: 3;
}

.diamond-shape__icon_rubl::after {
  position: absolute;
  top: -9px;
  left: calc(50% - 3px);
  z-index: 1;
  transform: rotate(45deg);
  width: 2px;
  height: 55px;
  background-color: #21262D;
  content: '';
}

.diamond-shape__icon svg {
  font-size: 30px;
}

