@media only screen and (max-width: 1023px) {
  .nav {
    flex-direction: column;
    justify-content: center;
    position: absolute;
    top: 80px;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    padding-bottom: 80px;
    overflow: auto;
    opacity: 0;
    border: 0;
    visibility: hidden;
    transform: translate3d(0, 100px, 0);
    transition: transform .4s ease, opacity .4s ease, visibility 0s .4s, -webkit-transform .4s ease;
    background: #fff;
    text-align: center;
    box-shadow: inset 0 0 25px rgba(33, 38, 45, 0.3);
  }
}

@media only screen and (max-width: 767px) {
  .nav {
    top: 65px;
    padding-bottom: 65px;
  }
}

.nav.is-active {
  opacity: 1;
  visibility: visible;
  transform: translate3d(0, 0, 0);
  transition: transform .4s ease, opacity .4s ease, visibility 0s 0s, -webkit-transform .4s ease;
}

.nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

@media only screen and (max-width: 1023px) {
  .nav__list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: auto;
    height: 100%;
    padding: 0;
  }
}

.nav__list-item {
  position: relative;
}

@media only screen and (max-width: 1023px) {
  .nav__list-item {
    width: 100%;
    opacity: 0;
    transform: translateY(20px);
    transition: .3s ease-in-out;
  }
}

.is-active .nav__list-item {
  opacity: 1;
  transform: translateY(0);
}

@media only screen and (max-width: 1023px) {
  .nav__list-item::before {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background: rgba(33, 38, 45, 0.3);
    content: '';
  }
}

.nav__list-item:not(:last-child) {
  margin-right: 23px;
}

.nav__list-item:not(:last-child)::after {
  position: absolute;
  top: 50%;
  width: 4px;
  right: -50px;
  height: 4px;
  margin-top: -2px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  content: '';
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:not(:last-child)::after {
    display: none;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(1) {
    transition-delay: 0s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(2) {
    transition-delay: 0.1s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(3) {
    transition-delay: 0.2s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(4) {
    transition-delay: 0.3s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(5) {
    transition-delay: 0.4s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(6) {
    transition-delay: 0.5s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(7) {
    transition-delay: 0.6s;
  }
}

@media only screen and (max-width: 1023px) {
  .nav__list-item:nth-child(8) {
    transition-delay: 0.7s;
  }
}

.nav__list-link {
  display: block;
  position: relative;
  font-family: "ProximaNova-Semibold", Arial, sans-serif;
  font-weight: normal;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0px;
  text-transform: uppercase;
  color: #6D788C;
}

.nav__list-link::before {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 1px;
  background: currentColor;
  opacity: .24;
  transform-origin: 100% 100%;
  transition: .3s ease-in-out;
  pointer-events: none;
  content: '';
