.screen {
   &__content {
      position: relative;
      height: 487px;
      &::after {
         position: absolute;
         top: 100%;
         left: 0;
         width: 100%;
         height: 121px;
         background-color: #D5B893;
         content: ''; } }
   &__title {
      max-width: 900px;
      margin: 0 auto 30px; }
   &__container {
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      text-align: center; }
   &__btn {
      min-width: 410px; }
   &__benefits {
      position: relative;
      z-index: 1;
      max-width: 1200px;
      margin: -73px auto 0; } }

.benefits {
   padding: 50px 30px 60px;
   background: $white;
   border: 1px solid #DCB385;
   box-shadow: 0 5px 10px rgba(0, 0, 0, 0.04);
   &__item {
      $parent: &;
      position: relative;
      text-align: center;
      line-height: 17px;
      color: #363C43;
      &:not(:last-child) {
         &::after {
            position: absolute;
            top: -18px;
            right: 0;
            width: 12px;
            height: 205px;
            background: url("../img/decor/vert-line.svg") no-repeat;
            content: ''; } }
      .diamond-shape__title {
         position: relative;
         top: 5px; }
      &:nth-child(2) {
         &::after {
            right: -10px; }
         .diamond-shape__title {
            font-size: 48px; } }
      &-diamond {
         margin-bottom: 23px; }
      &-title {}
      &-descr {
         max-width: 280px;
         margin: 0 auto;
         @include f('pn400', 14, 17);
         #{$parent}:nth-child(1) & {
            max-width: 240px; } }
      &-icon {} } }
